.news-view{padding-top:45px}.news-view .slide-left-enter,.news-view .slide-right-leave-to{opacity:0;-webkit-transform:translate(30px);transform:translate(30px)}.news-view .slide-left-leave-to,.news-view .slide-right-enter{opacity:0;-webkit-transform:translate(-30px);transform:translate(-30px)}.news-view .item-enter-active,.news-view .item-leave-active,.news-view .item-move{-webkit-transition:all .5s cubic-bezier(.55,0,.1,1);transition:all .5s cubic-bezier(.55,0,.1,1)}.news-view .item-enter,.news-view .item-leave-active{opacity:0;-webkit-transform:translate(30px);transform:translate(30px)}.news-view .item-leave-active{position:absolute}.news-list,.news-list-nav{background-color:#fff;border-radius:2px}.news-list-nav{padding:15px 30px;position:fixed;text-align:center;top:55px;left:0;right:0;z-index:998;box-shadow:0 1px 2px rgba(0,0,0,.1)}.news-list-nav a{margin:0 1em}.news-list-nav .disabled{color:#ccc}.news-list{margin:30px 0;width:100%;-webkit-transition:all .5s cubic-bezier(.55,0,.1,1);transition:all .5s cubic-bezier(.55,0,.1,1)}.news-list ul{list-style-type:none;padding:0;margin:0}@media (max-width:600px){.news-list{margin:10px 0}}